class Tabs {
    constructor(elem) {
        this.tabs = elem;
        this.tabItems = document.querySelectorAll('.tabs__item');
        this.tabContentItems = document.querySelectorAll('.tabs-content__item')
    }

    bindEvents() {
        if (this.tabItems.length) {
            this.tabItems.forEach(tab => {
                tab.addEventListener("click", () => {
                    this.tabItems.forEach(item => item.classList.remove('active-tab'))
                    tab.classList.add('active-tab');
                    this.activeTabContent(tab.id)
                })
            })
        }
    }

    activeTabContent(id) {
        this.tabContentItems.forEach(content => {
            content.classList.remove('active-content');
            if (content.id === id) {
                content.classList.add('active-content');
            }
        })
    }



    init() {
        this.bindEvents();
    }
}

if (document.querySelector('.tabs')) {
    new Tabs(document.querySelector('.tabs')).init();
}

