function init() {

  let locationMap = new ymaps.Map("locationMap", {
    center: [55.189881, 83.142107],
    zoom: 16,
    controls: ['fullscreenControl'],
    behaviors: ['drag', 'dblClickZoom', 'multiTouch']
  });

  let placemark = new ymaps.Placemark([55.189881, 83.142107], {
    hintContent: 'Новосибирская обл., п. Октябрьский, ул. Локтинская, д. 10/2',
    balloonContent: 'Новосибирская обл., п. Октябрьский, ул. Локтинская, д. 10/2',
    iconCaption: 'Локтинская улица, 14'
  });

  locationMap.geoObjects.add(placemark);
}

if(document.querySelector('.map')){
  ymaps.ready(init);
}

