class btnMenu {
  constructor(elem) {
    this.block = elem;
    this.btnMenu = this.block.querySelector('.btn-menu');
    this.menu = this.block.querySelector('.menu');
    this.btnClose = this.block.querySelector('.btn-close');
    this.bgMobileMenu = document.querySelector('.bg-mobile-menu');
    this.body = document.querySelector('body');
  }

  init() {
    this.openMenu();
    this.closeMenu();
  }

  openMenu() {
    this.btnMenu.addEventListener('click', (e) => {
      this.menu.classList.add('menu_js-active');
      this.bgMobileMenu.classList.add('bg-mobile-menu_js-active');
      this.btnClose.classList.add('btn-close_js-active');
      this.body.style.overflow = 'hidden';
      this.block.classList.add('js-active');
      this.bgMobileMenu.insertAdjacentElement('afterbegin', this.menu)
    });
  }

  closeMenu() {
    this.btnClose.addEventListener('click', () => {
      this.menu.classList.remove('menu_js-active');
      this.bgMobileMenu.classList.remove('bg-mobile-menu_js-active');
      this.btnClose.classList.remove('btn-close_js-active');
      this.body.style.overflow = '';
      this.block.classList.remove('js-active');
    });
  }
}

const container = document.querySelector('.header');
if(container){
  new btnMenu(container).init();
}
