import Choices from "choices.js";

class mainForm {
  constructor(elem) {
    this.block = elem;
    this.presonalForm = document.querySelector('.main-form_personal-form');
  }

  inputMask() {
    const inputMask = this.block.querySelector('[data-phone]')
    const prefixNumber = (str) => {
      if (str === "7") {
        return "7 (";
      }
      if (str === "8") {
        return "8 (";
      }
      if (str === "9") {
        return "7 (9";
      }
      return "7 (";
    };

    inputMask.addEventListener("input", () => {
      const value = inputMask.value.replace(/\D+/g, "");
      const numberLength = 11;

      let result;
      if (inputMask.value.includes("+8") || inputMask.value[0] === "8") {
        result = "";
      } else {
        result = "+";
      }
      for (let i = 0; i < value.length && i < numberLength; i++) {
        switch (i) {
          case 0:
            result += prefixNumber(value[i]);
            continue;
          case 4:
            result += ") ";
            break;
          case 7:
            result += "-";
            break;
          case 9:
            result += "-";
            break;
          default:
            break;
        }
        result += value[i];
      }
      inputMask.value = result;
    });
  }

  init() {
    let selectForm = this.block.querySelector(".form__select-leadership");
    if (selectForm) {
      new Choices(selectForm, {
        searchEnabled: false,
        itemSelectText: '',
        classNames: {
          containerInner: 'form__select-leadership'
        }
      })
    }


    this.inputMask();
  }
}

if (document.querySelector('.main-form')) {
  new mainForm(document.querySelector('.main-form')).init();
}

