class modalWindow {
  constructor (elem) {
    this.block = elem;
    this.modalContent = this.block.querySelector('.modal-window__content');
    this.nodeBody = document.querySelector('body');
    this.dataModals = document.querySelectorAll('[data-modal-window]');
    this.btnClose = this.block.querySelector('.modal-window__btn-close');
  }

  init() {
    this.readDataModals();
    this.closeModal();
  }

  readDataModals() {
    this.dataModals.forEach( modal => {
      modal.addEventListener('click', (e) => {
        e.preventDefault();
        let idModal = modal.getAttribute('data-modal-window');
        let modalWindow = document.getElementById(idModal);
        modalWindow.classList.add('js-open-modal');
        this.nodeBody.classList.add('js-overflow-hidden');
      });
    });
  }

  closeModal() {
    this.block.addEventListener('click', (event) => {
      if (!this.modalContent.contains(event.target)){
        this.block.classList.remove('js-open-modal');
        this.nodeBody.classList.remove('js-overflow-hidden');
      }
    })

    this.btnClose.addEventListener('click', () => {
      this.block.classList.remove('js-open-modal');
      this.nodeBody.classList.remove('js-overflow-hidden');
    })
  }
}


const elements = document.querySelectorAll('.modal-window');
if (elements.length) {
  Array.from(elements).map(el => new modalWindow(el).init());
}
